import Auth from '@/apis/auth'
import router from '@/router'


// const getDefaultState = () => ({
//     user: null,
//     notes: null
// })

// const state = getDefaultState()

const state = {
    user: null
}

const getters = {
    username: state => state.user === null ? '请登录' : state.user.username,

    slug: state => state.user === null ? '请登录' : (state.user.username.charAt(0) + state.user.username.charAt(1) + state.user.username.charAt(2) + '...')

}

const mutations = {
    // resetNote(state) {
    //     Object.assign(state, getDefaultState())
    // },
    setUser(state, payload) {
        state.user = payload.user
    }
}

const actions = {
    login({ commit }, { username, password }) {
        return Auth.login({ username, password })
            .then(res => {
                commit('setUser', { user: res.data })
            })
    },

    register({ commit }, { username, password }) {
        return Auth.register({ username, password })
            .then(res => {
                commit('setUser', { user: null })
            })
    },

    logout({ commit }, payload = { path: '/login' }) {
        return Auth.logout()
            .then(res => {
                commit('setUser', { user: null })
                commit('resetNote', null, { root: true })
                commit('resetTrash', null, { root: true })
                commit('resetNotebooks', null, { root: true })
                // location.reload(true)
                router.push(payload)
            })
    },

    checkLogin({ commit, state }, payload = { path: '/' }) {
        if (state.user !== null) return Promise.resolve()
        return Auth.getInfo()
            .then(res => {
                if (!res.isLogin) {
                    router.push(payload)
                } else {
                    commit('setUser', { user: res.data })
                }
            })
    }
}


export default {
    state,
    getters,
    mutations,
    actions
}


